import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    redirect: "/home"
  },
  {
    path: "/home",
    name: "home",
    component: () => import("@/views/HomeView.vue"),
    meta: { title: "首页" }
  },
  {
    path: "/classify",
    name: "classify",
    component: () => import("@/views/ClassifyView.vue"),
    meta: { title: "分类" }
  },
  {
    path: "/shoppingcar",
    name: "shoppingcar",
    component: () => import("@/views/ShoppingCar.vue"),
    meta: { title: "购物车" }
  },
  {
    path: "/mine",
    name: "mine",
    component: () => import("@/views/MineView.vue"),
    meta: { title: "我的" }
  },
  {
    path: "/search",
    name: "search",
    component: () => import("@/views/SearchView.vue"),
    meta: { title: "搜索" }
  },
  {
    path: "/searchlist/:searchText?",
    name: "searchlist",
    component: () => import("@/views/SearchList.vue"),
    meta: { title: "搜索列表" }
  },
  {
    path: "/detail/:id?",
    name: "/detail",
    component: () => import("@/views/GoodsDetail.vue"),
    meta: { title: "商品详情" }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/LoginView.vue"),
    meta: { title: "登录" }
  },
  {
    path: "/register",
    name: "register",
    component: () => import("@/views/RegisterView.vue"),
    meta: { title: "注册" }
  },
  {
    path: "/pay",
    name: "pay",
    component: () => import("@/views/PayView.vue"),
    meta: { title: "结算" }
  },
  {
    path:"/address",
    name:"address",
    component:()=>import("@/views/AddressView.vue"),
    meta: { title: "地址管理" }
  },
  {
    path:"/addressedit",
    name:"addressedit",
    component:()=>import("@/views/AddressEdit.vue"),
    meta: { title: "地址编辑" }
  },
  {
    path:"*",
    component:()=>import("@/views/NotFound.vue"),
    meta: { title: "404页面" }
  }
]




const router = new VueRouter({
  mode:"hash",
  routes
})
router.beforeEach((to, from, next) => {
  if(to.meta.title){
    document.title=to.meta.title
  }
  let userinfo = window.localStorage.getItem("userinfo")
  if (to.name == "mine" || to.name == "shoppingcar"||to.name=="pay" ||to.name =="addressedit" || to.name == "address") {
    if (userinfo) {
      next()
    } else {
      next("/login")
    }
  }
  

  next()
})

export default router
