import carModel from "@/model/carModel";
export default {
    namespaced: true,
    state: {
        list: []
    },
    mutations: {
        initList(state, payload) {
            state.list = payload.map(item => {
                item.checked = false
                return item
            })
        },
        addCarList(state, payload) {
            state.list = [...state.list, payload]
        },
        updataCheckedAll(state, payload) {
            state.list = state.list.map(item => {
                item.checked = payload
                return item
            })
        },
        updataCheckedOne(state, payload) {
            state.list = state.list.map(item => {
                if(item.id == payload.id){
                    item.checked=!payload.checked
                }
                return item
            })
        },
        changeNum(state, payload) {
            state.list = state.list.map(item => {
                if (item.id == payload.id) {
                    item.num = payload.num
                }
                return item
            })
        },
        delCarList(state) {
            state.list = state.list.filter(item => !item.checked);
        },
    },
    actions: {
        async initList({ commit }, payload) {
            let { data: list } = await carModel.query({
                userid: payload
            })
            commit("initList", list)
        },
        async addCarList({ commit }, payload) {
            let { data: list } = await carModel.add(payload)
            commit("addCarList", list)
        },
        async changeNum({ commit }, payload) {

            let { data } = await carModel.modify({
                num: payload.num
            }, payload.id)
            commit("changeNum", data)
        },
        async delCarList({ commit }, list) {
            let arr = list.filter(item => item.checked);
            for (let item of arr) {
                item.checked ? await carModel.del(item.id) : false
            }
            commit("delCarList")
        }
    },

    getters: {
        price(state) {
            let checkedList = state.list.filter(item => item.checked)
            if (checkedList.length) {
                return checkedList.map(item => item.price * item.num).reduce((a, b) => a + b)
            }

            return 0
        }
    }
}