import axios from "./index";
export default {
    query: (params) => axios({
        url: "/useraddress",
        method: "get",
        params
    }),
    add: (data) => axios({
        url: "/useraddress",
        method: "post",
        data
    }),
    modify: (data, id) => {
        return axios({
            url: "/useraddress/" + id,
            method: "patch",
            data
        })
    },
    del: (id) => axios({
        url: "/useraddress/" + id,
        method: "delete"
    })

}