import addressModel from "@/model/addressModel"
export default {
    namespaced: true,
    state: {
        list: [],
        choose: null
    },
    mutations: {
        initList(state, payload) {
            state.list = payload.map(item => {
                item.checked = false
                return item
            })
            if (payload.length) {
                let list = payload.filter(item => item.isDefault)
                if (list.length) {
                    state.choose = list[0]
                } else {
                    state.choose = payload[0]
                }
            }else{
                state.choose=null
            }
        },
        updataList(state, payload) {
            state.list = state.list.map(item => {
                if (item.id == payload.id) {
                    item = { ...payload }
                }
                return item
            });
        },
        addList(state, payload) {
            state.list = [...state.list, payload]
        },
        delList(state, payload) {
            state.list = state.list.filter(item => item.id != payload);
        },
        setChoose(state,payload){
            state.choose=payload
        }
    },
    actions: {
        async initList({ commit }, payload) {
            let { data: list } = await addressModel.query({
                userid: payload
            })
            commit("initList", list)
        },
        async updateList({ commit }, payload) {
            let { data: querylist } = await addressModel.query({
                userid: payload.userid,
                isDefault: true
            })
            if (querylist.length) {
                if (querylist[0].id != payload.id) {
                    let { data } = await addressModel.modify({
                        isDefault: false
                    }, querylist[0].id)
                    commit("updataList", data)
                }
            }
            let { data } = await addressModel.modify({ ...payload }, payload.id)
            commit("updataList", data)
        },
        async addList({ commit }, payload) {
            if (payload.isDefault) {
                let { data: querylist } = await addressModel.query({
                    userid: payload.userid,
                    isDefault: true
                })
                if (querylist.length) {
                    let { data } = await addressModel.modify({
                        isDefault: false
                    }, querylist[0].id)
                    console.log(data, "接收的值");
                    commit("updataList", data)
                }
            }
            let { data: list } = await addressModel.add(payload)
            commit("addList", list)


        },
        async delList({ commit }, payload) {//id
            await addressModel.del(payload)
            commit("delList", payload)
        }
    },

    getters: {
    }
}