<template>
  <div class="app">
    <router-view></router-view>
    <div class="tabbar" v-show="this.$store.state.isShow">
      <router-link to="/home"
        ><span class="iconfont icon-shouye"></span>商店</router-link
      >
      <router-link to="/classify"
        ><span class="iconfont icon-fenlei"></span>分类</router-link
      >
      <router-link to="/shoppingcar"
        ><span class="iconfont icon-gouwuche"></span>购物车</router-link
      >
      <router-link to="/mine"
        ><span class="iconfont icon-wode"></span>我的</router-link
      >
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      active: 1,
    };
  },
  methods: {
    ...mapActions(["getUserInfo"]),
    ...mapActions("shoppingcar", { shoppingcarInitList: "initList" }),
  },
  async created() {
    await this.getUserInfo();
    if (this.$store.state.userinfo.id) {
      await this.shoppingcarInitList(this.$store.state.userinfo.id);
    }
  },
};
</script>

