import Vue from 'vue'
import Vuex from 'vuex'
import shoppingcar from "./shoppingcar"
import address from './address'
import userModel from "@/model/userModel"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShow: true,
    goodsInfo: {

    },
    userinfo: {

    },
  },
  getters: {
  },
  mutations: {
    isShowTabbar(state, payload) {//布尔值是否显示
      state.isShow = payload
    },
    setGoodsInfo(state, payload) { //商品的对象
      state.goodsInfo = payload
    },
    getUserInfo(state, payload) {
      state.userinfo = payload
    },
    removeUserInfo(state) {
      window.localStorage.removeItem("userinfo");
      state.userinfo = {}
    }
  },
  actions: {
    async getUserInfo({ commit }) {
      let userinfo = window.localStorage.getItem("userinfo")
      let data = {}
      if (userinfo) {
        userinfo = JSON.parse(userinfo)
        let { data: list } = await userModel.query({
          id: userinfo.id
        })
        let flag = true
        if (list.length) {
          if (list[0].username != userinfo.username) {
            flag = false
          }
          if (list[0].userpwd != userinfo.userpwd) {
            flag = false
          }
        }
        if (flag) {
          data = userinfo
        }
      }
      commit("getUserInfo", data)

    }
  },
  modules: {
    shoppingcar,
    address
  }
})
